.text-editor-container .text-editor-dropdown {
  width: 100%;
}

.text-editor-container .text-editor-dropdown-menu {
  width: 100%;
  border-radius: 6px;
  max-height: 200px;
  overflow-y: scroll;
}

.text-editor-container .text-editor-dropdown-menu::-webkit-scrollbar {
  width: 0;
}

.text-editor-container .text-editor-dropdown-item {
  color: #212529;
  padding-left: 1rem;
  padding-right: 1rem;
  height: 30px;
  font-size: 14px;
}

.text-editor-container .text-editor-dropdown-item:hover {
  color: #fff;
  background-color: #20a0ff;
}

/* mobile css */
.text-editor-container .text-editor-dropdown-menu-mobile {
  width: 100%;
}

.camera-auto-fill-value {
  position: absolute;
  top: 9px;
  right: 6px;
  cursor: pointer;
  color: #666;
  background: #fff;
  display: flex;
  justify-content: center;
  width: 24px;
  height: 24px;
}

.mobile-custom-camera {
  width: 350px;
}

.mobile-custom-camera .am-modal-content {
  padding-top: 0;
  width: 350px;
  background: transparent;
}

.mobile-custom-camera .am-modal-content .am-modal-body {
  padding: 0;
  line-height: 1;
  height: 300px;
  overflow: hidden;
}

.mobile-custom-camera .custom-scan-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(#ddd, #ddd) left top,
    linear-gradient(#ddd, #ddd) left top,
    linear-gradient(#ddd, #ddd) right top,
    linear-gradient(#ddd, #ddd) right top,
    linear-gradient(#ddd, #ddd) right bottom,
    linear-gradient(#ddd, #ddd) right bottom,
    linear-gradient(#ddd, #ddd) left bottom,
    linear-gradient(#ddd, #ddd) left bottom;
  background-color: rgba(0,0,0,.4);
  background-repeat: no-repeat;
  background-size: 5px 50px, 50px 5px;
  border-radius: 12px;
  overflow: hidden;
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

.mobile-custom-camera .custom-scan-container>video {
  background: #fff;
  object-fit: cover;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.mobile-custom-camera .custom-camera-line {
  position: absolute;
  width: 80%;
  left: 10%;
  border: none;
  top: 0;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(32, 160, 255, 0.3) 20%, 
    #20a0ff 50%, 
    rgba(32, 160, 255, 0.3) 80%, 
    transparent 100%
  );
  box-shadow: 
    0 0 6px rgba(32, 160, 255, 0.5),
    0 0 12px rgba(32, 160, 255, 0.3),
    0 0 18px rgba(32, 160, 255, 0.15);
  z-index: 10;
  border-radius: 1px;
  transform: translateZ(0);
  will-change: top, opacity, transform;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes scanCode {
  0% {
    top: 0;
    opacity: 0.6;
    transform: scaleY(1);
  }
  15% {
    opacity: 1;
    transform: scaleY(1.2);
  }
  85% {
    opacity: 1;
    transform: scaleY(1.2);
  }
  100% {
    top: 100%;
    opacity: 0.6;
    transform: scaleY(1);
  }
}

@keyframes pulse {
  0%, 100% {
    box-shadow: 
      0 0 6px rgba(32, 160, 255, 0.5),
      0 0 12px rgba(32, 160, 255, 0.3),
      0 0 18px rgba(32, 160, 255, 0.15);
  }
  50% {
    box-shadow: 
      0 0 8px rgba(32, 160, 255, 0.7),
      0 0 16px rgba(32, 160, 255, 0.4),
      0 0 24px rgba(32, 160, 255, 0.2);
  }
}

@media screen and (max-width: 767.8px) {
  .text-editor-container .text-editor-dropdown-menu {
    top: -40px !important;
  }
  .text-editor-container .text-editor-dropdown-menu-mobile {
    top: -44px !important;
  }
}
